@mixin boxDisplaySettings {
  display: flex;
  justify-content: center;
  height: 80%;
  width: 80%;

}

@mixin boxDisplaySettingsRWD {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  width: 90%;

}

@mixin wrapSettings {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin wrapSettingRWD {
  width: 100vw;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

@mixin navBarBackgroundSettings {
  \background: rgba(77, 158, 234, 1);
  background: -moz-linear-gradient(top, rgba(77, 158, 234, 1) 0%, rgba(77, 158, 234, 1) 39%, rgba(70, 130, 180, 1) 100%);
  background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(77, 158, 234, 1)), color-stop(39%, rgba(77, 158, 234, 1)), color-stop(100%, rgba(70, 130, 180, 1)));
  background: -webkit-linear-gradient(top, rgba(77, 158, 234, 1) 0%, rgba(77, 158, 234, 1) 39%, rgba(70, 130, 180, 1) 100%);
  background: -o-linear-gradient(top, rgba(77, 158, 234, 1) 0%, rgba(77, 158, 234, 1) 39%, rgba(70, 130, 180, 1) 100%);
  background: -ms-linear-gradient(top, rgba(77, 158, 234, 1) 0%, rgba(77, 158, 234, 1) 39%, rgba(70, 130, 180, 1) 100%);
  background: linear-gradient(to bottom, rgba(77, 158, 234, 1) 0%, rgba(77, 158, 234, 1) 39%, rgba(70, 130, 180, 1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#4d9eea', endColorstr='#4682b4', GradientType=0);

}

@mixin shadowDownSettings {
  -webkit-box-shadow: 0px 14px 19px -18px rgba(0, 0, 0, 0.54);
  -moz-box-shadow: 0px 14px 19px -18px rgba(0, 0, 0, 0.54);
  box-shadow: 0px 14px 19px -18px rgba(0, 0, 0, 0.54);
}

@mixin imgShadowSettings {
  -webkit-box-shadow: 3px 3px 20px 0px rgba(0, 0, 0, 0.45);
  -moz-box-shadow: 3px 3px 20px 0px rgba(0, 0, 0, 0.45);
  box-shadow: 3px 3px 20px 0px rgba(0, 0, 0, 0.45);
}

@mixin newBackgroundColor {
  background-color: #4D9EEA;
  -webkit-clip-path: polygon(15% 0, 30% 0, 0 30%, 0 15%);
  clip-path: polygon(12% 0, 25% 0, 0 25%, 0 12%);

}

@mixin inputHoverShadow {
  -webkit-animation: shadow-drop-2-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  animation: shadow-drop-2-center 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
  @-webkit-keyframes shadow-drop-2-center {
    0% {
      -webkit-transform: translateZ(0);
      transform: translateZ(0);
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      -webkit-transform: translateZ(50px);
      transform: translateZ(50px);
      -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
      box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }
  @keyframes shadow-drop-2-center {
    0% {
      -webkit-transform: translateZ(0);
      transform: translateZ(0);
      -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
    }
    100% {
      -webkit-transform: translateZ(50px);
      transform: translateZ(50px);
      -webkit-box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
      box-shadow: 0 0 20px 0px rgba(0, 0, 0, 0.35);
    }
  }

}

@mixin inputsShadow {
  -webkit-box-shadow: 0px 0px 14px 2px rgba(0, 0, 0, 0.04);
  -moz-box-shadow: 0px 0px 14px 2px rgba(0, 0, 0, 0.04);
  box-shadow: 0px 0px 14px 2px rgba(0, 0, 0, 0.04);
}

